<template>
  <div class="video-chat">
    <nav class="video-chat-toolbar">
      <input v-model="username" type="text" placeholder="Choose a name" />
      <button @click="connect" class="btn">Connect</button>
      <p v-if="connected">Connected</p>
      <p v-else>Not Connected</p>
      <input v-model="friendname" type="text" placeholder="Who you want to call" />
      <button @click="call" class="btn">Call</button>
    </nav>
    <div class="video-chat-body">
      <video id="userVideo" autoplay></video>
      <video id="friendVideo" autoplay></video>
    </div>
  </div>
</template>

<script>
import io from "socket.io-client";

export default {
  name: "videoChat",
  data() {
    return {
      username: "",
      connected: false,
      friendname: "",
      peerConnection: null,
      socket: null
    };
  },
  methods: {
    send(data) {
      this.socket.emit("peerData", data);
    },
    connect() {
      this.socket = io.connect("worldofcalculators.com/", {
        path: "/my-servers/video-chat/"
      });
      this.socket.on("connect", () => {
        this.socket.emit("user-connect", {
          username: this.username
        });
        this.connected = true;
      });
      this.socket.on("handshake", async data => {
        // console.log(data);
        if (data.offer) {
          this.applyHandshake(null, JSON.parse(data.offer));
        }
        if (data.candidate) {
          this.applyHandshake(JSON.parse(data.candidate), null);
        }
        if (data.forAnswer) {
          const answer = await this.peerConnection.createAnswer();
          await this.peerConnection.setLocalDescription(answer);
          this.send({
            sender: this.username,
            receiver: this.friendname,
            offer: JSON.stringify(answer)
          });
        }
      });

      this.socket.on("incoming", data => {
        this.friendname = data.sender;
        //auto answer xD
        this.answer();
      });
    },
    applyHandshake(candidate, description) {
      // console.log("APPLY", candidate, description);
      if (description) {
        this.peerConnection.setRemoteDescription(description);
      }
      if (candidate) {
        this.peerConnection.addIceCandidate(candidate);
      }
    },
    async call() {
      this.socket.emit("make-call", {
        sender: this.username,
        receiver: this.friendname
      });
      const stream = await navigator.mediaDevices.getUserMedia({
        video: {
          width: { min: 640, max: 1920 },
          height: { min: 400, max: 1080 },
          aspectRatio: 1.777777778,
          frameRate: { max: 30 }
        },
        audio: {
          sampleSize: 16,
          channelCount: 2
        }
      });
      document.getElementById("userVideo").srcObject = stream;

      this.peerConnection = new RTCPeerConnection({
            iceServers: [
                {
                    urls: ['turn:my-turn-server.mycompany.com:19403'],
                    username: 'optional-username',
                    credentials: 'auth-token'
                },
                {
                  urls: ["turn:turn01.hubl.in?transport=udp"]
                },
                {
                  urls: ["turn:turn02.hubl.in?transport=tcp"]
                },
                {
                    urls: ['turn:numb.viagenie.ca'],
                    credential: 'muazkh',
                    username: 'webrtc@live.com'
                },
                {
                    urls: ['turn:192.158.29.39:3478?transport=udp'],
                    credential: 'JZEOEt2V3Qb0y27GRntt2u2PAYA=',
                    username: '28224511:1379330808'
                },
                {
                    urls: ['turn:192.158.29.39:3478?transport=tcp'],
                    credential: 'JZEOEt2V3Qb0y27GRntt2u2PAYA=',
                    username: '28224511:1379330808'
                },
                {
                    urls: ['turn:turn.bistri.com:80'],
                    credential: 'homeo',
                    username: 'homeo'
                 },
                 {
                    urls: ['turn:turn.anyfirewall.com:443?transport=tcp'],
                    credential: 'webrtc',
                    username: 'webrtc'
                },
                {
                  "urls": ["turn:13.250.13.83:3478?transport=udp"],
                  "username": "YzYNCouZM1mhqhmseWk6",
                  "credential": "YzYNCouZM1mhqhmseWk6"
                }
            ]
        });

      for (const track of stream.getTracks()) {
        this.peerConnection.addTrack(track, stream);
      }

      this.peerConnection.ontrack = e => (document.getElementById("friendVideo").srcObject = e.streams[0]);

      const offer = await this.peerConnection.createOffer();
      await this.peerConnection.setLocalDescription(offer);
      //Send Offer.
      this.send({
        sender: this.username,
        receiver: this.friendname,
        offer: JSON.stringify(offer),
        candidate: null,
        forAnswer: true
      });

      //Send icecandidate
      this.peerConnection.onicecandidate = e => {
        this.send({
          sender: this.username,
          receiver: this.friendname,
          candidate: JSON.stringify(e.candidate),
          offer: null
        });
      };
    },
    async answer() {
      const stream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true });
      document.getElementById("userVideo").srcObject = stream;
      this.peerConnection = new RTCPeerConnection();
      for (const track of stream.getTracks()) {
        this.peerConnection.addTrack(track, stream);
      }
      this.peerConnection.ontrack = e => (document.getElementById("friendVideo").srcObject = e.streams[0]);

      this.peerConnection.onicecandidate = e => {
        this.send({
          sender: this.username,
          receiver: this.friendname,
          candidate: JSON.stringify(e.candidate)
        });
      };
    }
  }
};
</script>

